.i {
    display: flex;
    height: 100vh;
}

.i-left {
    flex: 1;
    position: relative;
}

.i-right {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right-wrapper {
    padding: 20px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro {
    font-size: 30px;
    font-weight: 300;
}

.i-name {
    font-size: 60px;
}

.i-title {
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper {
    height: 100%;
    animation: move 10s ease-in-out infinite alternate;
}

@keyframes move {
    50% {
        transform: translateY(-50px);
    }

    100% {
        transform: translateY(-100px);
    }
}

.i-title-item {
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #59b256;
    display: flex;
    align-items: center;

}

.i-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    position: absolute;
}

.i-bg {
    clip-path: polygon(75% 0%, 100% 41%, 75% 100%, 0% 100%, 0 41%, 0% 0%);
    background-color: #59b256;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
}


@media screen and (max-width: 480px) {
    .i {
        flex-direction: column;
    }

    .i-bg{
        background-color: white;
    }

    .i-right-wrapper {
        padding: 10px;
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }

    .i-name{
        color: #003300;
    }

    .i-title-item{
        color: gray;
    }

    .i-desc{
        display: none;
    }
}